.animatedBarsContainer{	
	-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54);	
	-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54);	
	transition:all 0.3s cubic-bezier(.82,.16,.16,.54);
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	/* opacity:0.5; */
	display: flex;
	-ms-flex-align: end;
	    align-items: flex-end;
	margin-left:5px;
	height:10px;

}
	.animatedBarsContainer .bar{
		-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54);
		-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54);
		transition:all 0.3s cubic-bezier(.82,.16,.16,.54);
		margin:0 1px 0 0;
		height:10px;
		width:3px;
		background:rgb(255,255,255);
	    -webkit-animation-name: example;
	            animation-name: example;
	    -webkit-animation-duration: 1s;
	            animation-duration: 1s;
	    -webkit-animation-iteration-count: infinite;
	            animation-iteration-count: infinite;
	}
	.animatedBarsContainer .bar:nth-child(1) {
	    -webkit-animation-delay: -.33s;
	            animation-delay: -.33s;
	}
	.animatedBarsContainer .bar:nth-child(2) {
	    -webkit-animation-delay: -.66s;
	            animation-delay: -.66s;
	}
	.animatedBarsContainer .bar:nth-child(4) {
	    -webkit-animation-delay: -.20s;
	            animation-delay: -.20s;
	}
	.animatedBarsContainer .bar:nth-child(5) {
	    -webkit-animation-delay: -.8s;
	            animation-delay: -.8s;
	}


@-webkit-keyframes example {
    0%   {height: 50%;}  
    50%  {height: 100%;}
    100% {height:50%;}
}


@keyframes example {
    0%   {height: 50%;}  
    50%  {height: 100%;}
    100% {height:50%;}
}


