.subNavGrad{
	width:100%;
	height:20px;
	position: fixed;
	left:0;
	top:50px;
	
	background: -webkit-gradient(
		linear,
		left top, left bottom,
		from(rgba(255,255,255,1)),
		to(rgba(255,255,255,0))
	);
	
	background: -webkit-linear-gradient(
		top,
		rgba(255,255,255,1),
		rgba(255,255,255,0)
	);
	
	background: -o-linear-gradient(
		top,
		rgba(255,255,255,1),
		rgba(255,255,255,0)
	);
	
	background: linear-gradient(
		to bottom,
		rgba(255,255,255,1),
		rgba(255,255,255,0)
	);
}
.navBarContainer{
	position: fixed;
	top:0;
	left:0;
	right:0;
	padding:0 20px;
	z-index: 100000000000;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	width:100%;
	height:50px;
	background:#111111;
	display: -ms-flexbox;
	display: -webkit-box;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: row;
	flex-direction: row;
	z-index: 1000000000000;
/* 	h3{
		
		font-size:16px;
		text-transform: uppercase
	} */
}
.navBarContainer .nameContainer{
		position: absolute;
		top:0;
		right:0px;
		-webkit-box-sizing: border-box;
		        box-sizing: border-box;
		height:100%;
		width:100%;
		-ms-flex-align: center;
		    align-items: center;
		-ms-flex-pack: end;
		    justify-content: flex-end;
		display: -ms-flexbox;
		display: -webkit-box;
		display: flex;
		-ms-flex-direction: row;
		flex-direction: row;
	}
.navBarContainer .nameContainer .surprises{
			font-famiy:Playfair Display!important;
			padding-left:60px;
			z-index: 10000000;
			position: absolute;
			bottom:0;
			left:0;
			padding-right:10px;
			/*text-transform: uppercase;*/
			opacity:0;
			overflow: hidden;
			pointer-events:none;
			z-index: 100000;
			color:#fefefe;

			margin:0;
			line-height: 50px;
		}
.navBarContainer .nameContainer .surprises span{
				opacity:0;
			}
.navBarContainer .nameContainer .nameBackground{
			right:0;
			bottom:0;
			position: absolute;
			width:0%;
			z-index: 1;
			height:2px;
			background:#555555;
			pointer-events:none;
		}
.navBarContainer .nameContainer .nameContentContainer{
			z-index: 2;
			position: absolute;
			right:60px;
			top:0;
			height:100%;
			cursor: pointer;
			display: -ms-flexbox;
			display: -webkit-box;
			display: flex;
			-ms-flex-direction: row;
			flex-direction: row;
			-ms-flex-align: center;
			    align-items: center;
		}
.navBarContainer .nameContainer .nameContentContainer .name{
				/* opacity:0.5; */
				margin-right:5px;
				-webkit-transition: all 0.1s ease 0.3s;
				-o-transition: all 0.1s ease 0.3s;
				transition: all 0.1s ease 0.3s;
				text-align: right;
			}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer{
					height:20px;
				}
/* opacity:0.51; */
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(1) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.3s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.3s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.3s;
					   /*background:red;*/
					   width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(2) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.6s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.6s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.6s;
					    /*background:orange;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(3) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.9s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.9s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)0.9s;
					    /*background:yellow;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(4) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.2s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.2s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.2s;
					    /*background:green;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(5) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.5s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.5s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.5s;
					    /*background:blue;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(6) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.8s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.8s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)1.8s;
					    /*background:indigo;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .animatedBarsContainer .bar:nth-child(7) {
						-webkit-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)2.1s;
						-o-transition:all 0.3s cubic-bezier(.82,.16,.16,.54)2.1s;
						transition:all 0.3s cubic-bezier(.82,.16,.16,.54)2.1s;
					    /*background:violet;*/
					    width:15px;
					}
.navBarContainer .nameContainer .nameContentContainer:hover .bar{
					height:200%;
				}
.navBarContainer .nameContainer .nameContentContainer:hover .name{
					-webkit-transition: none;
					-o-transition: none;
					transition: none;
					/*width:0;*/
					opacity:0;
				}
.navBarContainer .nameContainer .nameContentContainer:hover + div{
					width:100%;
					height:100%;
					-webkit-transition:width 0.3s cubic-bezier(.82,.16,.16,.54), height 0.3s linear 2.4s;
					-o-transition:width 0.3s cubic-bezier(.82,.16,.16,.54), height 0.3s linear 2.4s;
					transition:width 0.3s cubic-bezier(.82,.16,.16,.54), height 0.3s linear 2.4s;
					/* background:$grey3; */
				}
.navBarContainer .nameContainer .nameContentContainer:hover + div .surprises{
						font-size:16px;
						font-famiy:Playfair Display;
						text-transform:none;
						-webkit-transition: width 0.3s cubic-bezier(.82,.16,.16,.54) 2.7s, opacity 0.2s cubic-bezier(.82,.16,.16,.54) 2.8s;
						-o-transition: width 0.3s cubic-bezier(.82,.16,.16,.54) 2.7s, opacity 0.2s cubic-bezier(.82,.16,.16,.54) 2.8s;
						transition: width 0.3s cubic-bezier(.82,.16,.16,.54) 2.7s, opacity 0.2s cubic-bezier(.82,.16,.16,.54) 2.8s;
						width:auto;

						z-index: 100000000;
						/* opacity:0.5; */
					}
.navBarContainer .nameContainer .nameContentContainer:hover + div .surprises span:nth-child(1){
							-webkit-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.0s;
							-o-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.0s;
							transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.0s;
							opacity:1;
						}
.navBarContainer .nameContainer .nameContentContainer:hover + div .surprises span:nth-child(2){
							-webkit-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							-o-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							opacity:1;
						}
.navBarContainer .nameContainer .nameContentContainer:hover + div .surprises span:nth-child(3){
							-webkit-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							-o-transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							transition: opacity 0.3s cubic-bezier(.82,.16,.16,.54) 3.3s;
							opacity:1;
						}
.navBarContainer .linkContainer .link{
			/*z-index: 1;*/
			position: relative;
			display: inline-block;
			text-transform: uppercase;
			font-weight: bold;
			text-decoration: none;
			
		}
/* opacity:0.5; */
.navBarContainer .linkContainer .link .linkText{
				font-size:16px;
				color:#fefefe;
				/* opacity:0.5; */
				margin:0;
				padding:22px 40px;
			}
.navBarContainer .linkContainer .link .underline{
				-webkit-transition: height 0.3s cubic-bezier(.82,.16,.16,.54);
				-o-transition: height 0.3s cubic-bezier(.82,.16,.16,.54);
				transition: height 0.3s cubic-bezier(.82,.16,.16,.54);
				z-index: -1;
				height:5px;
				width:0%;
				background:#999999;
				position: absolute;
				bottom:0;
				left:0;	
			}
.navBarContainer .linkContainer .link:hover .linkText,.navBarContainer .linkContainer .link:active .linkText{
					opacity:1;
					color:#111111;
					-webkit-transition: all 0.3s cubic-bezier(.82,.16,.16,.54) 0.25s;
					-o-transition: all 0.3s cubic-bezier(.82,.16,.16,.54) 0.25s;
					transition: all 0.3s cubic-bezier(.82,.16,.16,.54) 0.25s;
				}
.navBarContainer .linkContainer .link:hover .underline,.navBarContainer .linkContainer .link:active .underline{
					-webkit-transition: width 0.2s cubic-bezier(.82,.16,.16,.54), height 0.2s cubic-bezier(.82,.16,.16,.54) 0.2s;
					-o-transition: width 0.2s cubic-bezier(.82,.16,.16,.54), height 0.2s cubic-bezier(.82,.16,.16,.54) 0.2s;
					transition: width 0.2s cubic-bezier(.82,.16,.16,.54), height 0.2s cubic-bezier(.82,.16,.16,.54) 0.2s;
					width:100%;
					height:100%;
				}
@media screen and (max-width: 600px){
	.navBarContainer{
		padding:0;
	}
			.navBarContainer .nameContainer .surprises{
				padding-left:20px;
			}
			.navBarContainer .nameContainer .nameContentContainer{
				right:20px;
			}

				.navBarContainer .nameContainer .nameContentContainer span{
					display:none;
				}
				.navBarContainer .linkContainer .link .linkText{
					max-width: 50px;
					padding:22px 20px;
				}
}
@media screen and (max-width: 350px){
	.navBarContainer{
		padding:0;
	}
			.navBarContainer .nameContainer .surprises{
				padding-left:10px;
			}
			.navBarContainer .nameContainer .nameContentContainer{
				right:10px;
			}
				.navBarContainer .nameContainer .nameContentContainer span{
				display:none;
			}
				.navBarContainer .linkContainer .link .linkText{
					max-width: 50px;
					padding:22px 10px;
				}
}


